.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.canvas-container{
  width: 100% !important;
  height: 30vh!important;
}
.lower-canvas{

  width: 100% !important;
  height: 100% !important;
}
.upper-canvas{
  
  width: 100% !important;
  height: 100% !important;

}

.react-datepicker__day--selected {
  background-image: linear-gradient(to top, #ffcccc, #ff8383)!important; 
}

input:focus {
  outline: 2px solid  rgb(255, 188, 200);
}